import React from 'react';
import phone from '../img/phone.png';
import shopiphone from '../img/shopiphone.svg';
import shopandroid from '../img/shopandroid.svg';


const Shopping =()=>{
     return(
     		 <>
     		    <div className="shopping-section">
					 <div className="row">
						<div className="col-sm-12 col-md-6 col-lg-6 shopping-left">
						<div className="icon">
							<h3 className="mt-5  text-left">Shopping and delivery made easy, Get all orders in 20 mins</h3>
							    <p className="my-4 download-text">Download Jeetar On</p>
								<a  href="" alt="iphone"> <img src={shopiphone} /></a>
								<a  href="" alt="android" className="ms-5"> <img src={shopandroid} /></a>
							</div>
						</div>
						<div className="col-sm-12 col-md-6 col-lg-6 shopping-right">
						   <img src={phone}  alt="fphone"/>

						</div>
					 </div>
					
                 </div>
     		 </>
     	)
}

export default Shopping