import React from 'react';
import bag from './../img/bag.svg';
import man from './../img/man.svg';
import herojeetarlogo from './../img/herojeetarlogo.svg';
import continueRule from './../logo/continueRule.svg';
import goods from './../img/goods right.svg';
import facebook from './../logo/facebook.svg';
import google from './../logo/google.svg';
import styled from 'styled-components';

const Herologo=styled.img`
   margin-left: 100px;
   margin-top: 76px;
   @media(max-width:1024px){
      margin-left:5px;
   }

`

const Hero =()=>{
     return(
         <>
           <div className="hero-section">
              <div className="row">
                  <div className="col-sm-12 col-md-7 col-lg-7">
                     <div className="container hero-left">
                        <Herologo src={herojeetarlogo} alt="logo" className="mb-4 herologo"/>
                        <h1 className="fast-shoping">Fast Shopping, <br></br>Fast Shipping in Best Time</h1>
                        <div className="image-wrapper">
                           <img src={bag} alt="ilustration1" className="ilustration1" />
                           <img src={man} alt="ilustration2"  className="ilustration2"/>
                        </div>
                     </div>
                    
                  </div>
                  <div className="col-sm-12 col-md-5 col-lg-5 hero-right">
                     <div className="form-panel">

                        <form  onSubmit={()=>{alert('submit')}} needs-validation="true" className="needs-validation" >
                           <div className="container">
                           <div className="form-group">
                              <h4 className="text-center py-2">Login or Register</h4>
                           </div>
                           <div className="row g-2">
                              <div className="col-3">
                                 <select className="form-select form-field">
                                    <option value="+234">+234</option>
                                 </select>
                              </div>
                              <div className="col-9">
                                 <input type="text" className="form-control form-field" placeholder="Phone Number"/>
                              </div>
                              <div className="col-12 form-group">
			           	            <button type="submit" className="continue-with-phone form-control btn-primary">Continue With Phone Number</button>
			                     </div>
                              <div className="col-12 form-group">
                                 <img src={continueRule} alt="continue" className="d-block mx-auto" />
                              </div>
                              <div className="col-12 form-group text-center">
                                 <a href="#"><img src={google} className="mx-3" alt="google"/></a>
                                 <a href="#"><img src={facebook} className=" mx-3" alt="facebook"/></a>
                              </div>
                           </div> 
                           </div>        
                        </form>
                        

                     </div>

                     <img src={goods} alt="goods" className="goods" />
                  </div>
              </div>
           </div>
         </>
      )
}

export default Hero