import React from 'react';
import styled from 'styled-components';
import textlogo from './../logo/textlogo.svg';
import continueRule from './../logo/continueRule.svg';
import facebook from './../logo/facebook.svg';
import google from './../logo/google.svg';
import cancel from './../logo/cancel.svg';


const Register = styled.p`
	font-family: Gilmer Bold;
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	text-align: center;
	margin-buttom: 10px;
	color: #374B58;	
`



const Login = ()=>{

	const loginHandler = (event)=>{
		console.log('hello');					
	}
	return(
		   <>
			<div className="modal fade" id="loginModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
			  <div className="modal-dialog">
			    <div className="modal-content container">
			      <div className="mt-5 border-0">
			        <img src={cancel} type="button" className="close" alt="cancel" data-bs-dismiss="modal" aria-label="Close" />
			      </div>
			      <div className="modal-body mt-n5">
			       <Register>
				   		Login or Register
				   </Register>

			        <form  onSubmit={loginHandler} needs-validation="true" className="needs-validation" >
			           <div className="row g-2">
			              <div className="col-3">
			           	    <select className="form-select form-field">
			           	      <option value="+234">+234</option>
			           	    </select>
			              </div>
			              <div className="col-9">
			           	    <input type="text" className="form-control form-field" placeholder="Phone Number"/>
			              </div>
			           </div>
			           
					   <div className="col-12 form-group">
			           	  <button type="submit" className="submit form-control btn-primary">Continue With Phone Number</button>
			           </div>
			           <div className="col-12 form-group">
			              <img src={continueRule} className="d-block mx-auto" />
			           </div>
			           <div className="col-12 form-group text-center">
			              <a href="#"><img src={google} className="mx-3" alt="google"/></a>
			              <a href="#"><img src={facebook} className=" mx-3" alt="facebook"/></a>
			           </div>

			        </form>
			         
			      </div> 
			  
			    </div>
			  </div>
			</div>
		   </>
		);
}

export default Login;